<template>
	<div id="chart-cpu" style="margin0;absolute;top: 0;left: 0;bottom: 0;right: 0; margin:auto; width: 100%;height: 100%">
	</div>
</template>
<script>
	// 引用axios
	import axios from 'axios'

	export default {
		name: 'echarts',
		data() {
			return {}
		},
		mounted() {
			// this.demo
			this.drawCpuLine()
		},
		methods: {
			drawCpuLine() {
				
				var datas = [];
				var times = [];

				// 	// 发ajax请求，用以获取数据，

					axios({
						url: "/getHistoryCpuUsage",
						method: "get",
						params: {}
					}).then(function(result) {
						// 遍历对象集合
						  var infos = result.data;
						  for(var i=0;i<infos.length;i++){ 
						        datas.push(infos[i].cpu_usage);
								times.push(infos[i].time);
								console.log("time: " + datas[i] + "usage: " + times[i]);
						    } 
					}).catch(function(error) {
						alert(error.data);
					}) 
					alert(datas)
					alert(times)

				// 基于准备好的dom，初始化echarts实例
				let myChart = this.$echarts.init(document.getElementById('chart-cpu'))

				var option = {
					tooltip: {
						trigger: 'axis',
						position: function(pt) {
							return [pt[0], '10%'];
						}
					},
					title: {
						left: 'center',
						text: 'e3base03号主机CPU实时监控图',
					},
					toolbox: {
						feature: {
							dataZoom: {
								yAxisIndex: 'none'
							},
							restore: {},
							saveAsImage: {}
						}
					},
					xAxis: {
						type: 'category',
						// 图表两边是否留空
						boundaryGap: false,
						data: times
					},
					yAxis: {
						type: 'value',
						boundaryGap: [0, '100%']
					},
					// 放大拉框
					dataZoom: [{
					    type: 'inside',
					    start: 0,
					    end: 10
					}, {
					    start: 0,
					    end: 10,
					    handleIcon: 'M10.7,11.9v-1.3H9.3v1.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4v1.3h1.3v-1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z',
					    handleSize: '80%',
					    handleStyle: {
					        color: '#fff',
					        shadowBlur: 3,
					        shadowColor: 'rgba(0, 0, 0, 0.6)',
					        shadowOffsetX: 2,
					        shadowOffsetY: 2
					    }
					}],
					series: [{
						name: 'CPU使用率',
						type: 'line',
						smooth: true,
						symbol: 'none',
						sampling: 'average',
						itemStyle: {
							color: 'rgb(255, 70, 131)'
						},
						areaStyle: {
							color: new(this.$echarts).graphic.LinearGradient(0, 0, 0, 1, [{
								offset: 0,
								color: 'rgb(255, 158, 68)'
							}, {
								offset: 1,
								color: 'rgb(255, 70, 131)'
							}])
						},
						data: datas
					}]
				}

				//绘制图表
				myChart.setOption(option);

				// dymatic
				//  setInterval(function() {
				// 	// X轴当前时间更新
				// 	var axisData = (new Date()).toLocaleTimeString().replace(/^\D*/, '');
				// 	option.xAxis.data.shift();
				// 	option.xAxis.data.push(axisData);

				// 	// 动态数据更新处
				// 	var data0 = option.series[0].data;
				// 	//value值
				// 	data0.shift();
				// 	data0.push(Math.round(Math.random() * 1000));

				// 	myChart.setOption(option);
				// }, 2100); 
			}
		}
	}
</script>

